@import url(https://fonts.googleapis.com/css?family=Exo+2:200i);
body {
  background-color: gray;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.btnStyle{align-items:center;background:linear-gradient(-45deg, rgba(0,0,0,0.22), rgba(255,255,255,0.25));box-shadow:12px 12px 16px 0 rgba(0,0,0,0.25),-8px -8px 12px 0 rgba(255,255,255,0.3);border-radius:50px;min-height:2em;width:25%;max-width:6em;margin:5px;font-size:xx-large;color:black;transition:.1s}.btnStyle:active{color:#cecece;box-shadow:inset 6px 6px 10px 0 rgba(0,0,0,0.2),inset -6px -6px 10px 0 rgba(255,255,255,0.5);transition:.1s}

